@charset "utf-8";

/* App.css */
.load_img{
  width: 100%;
}
/* 피시버전 */

@media screen and (min-width:768px) {
  .header{
    position: fixed;
    left: 0px; top: 0px;
    background: #000;
    width: 180px;
    height: 100vh;
  }
  .header > h1{
    text-align: center;
  }
  .header > h1 >img{
    width: 90%;
    padding: 15px 0px;
  }
  main{
    position: absolute;
    width: calc(100% - 180px);
    left: 180px;
  }
}

/* 모바일 버전 */
@media screen and (max-width:767px) {
  .header{
    /* position: absolute; */
    width: 100%; 
    background: #000;
    height: 100px;
  }
  .header h1{
    text-align: center;
  }
  .header h1 img{
    height: 50px;
  }
}